const app = getApp()

export default {
	data() {
		return {
			followlist: [],
			followindex: 0,
			current: 0,
			more: true,
			tabs: [
				"关注"
			]
		};
	},
	onLoad() {
		uni.request({
			url: app.globalData.apiurl + 'users/follow', //仅为示例，并非真实接口地址。
			data: {
				index: this.followindex
			},
			header: {
				'content-type': 'application/json', //自定义请求头信息
				'cookie': app.globalData.cookie
			},
			success: (res) => {
				console.log(res);
				res.data.list.forEach(item => {
					item.follow = true
				})
				this.followlist = res.data.list
				this.more = res.data.more
			}
		});
		uni.showShareMenu({})
	},
	onShareAppMessage(res) {
		return {
			title: app.globalData.sharetitle,
			path: '/pages/index/index',
			imageUrl: app.globalData.shareimg
		}
	},
	methods: {
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		tabSelect(e) {
			this.current = e.currentTarget.dataset.id;
		},
		navChange(index) {
			this.current = index;
		},
		swiperChange(e) {
			this.current = e.detail.current;
		},
		sponsorPage(index) {
			uni.navigateTo({
				url: "../sponsor/sponsor?id=" + this.followlist[index].id
			})
		},
		follow(index) {
			uni.request({
				url: app.globalData.apiurl + 'users/follow/' + this.followlist[index].name, //仅为示例，并非真实接口地址。
				method: 'POST',
				header: {
					'content-type': 'application/json', //自定义请求头信息
					'cookie': app.globalData.cookie
				},
				success: (res) => {
					console.log(res.data);
					this.followlist[index].follow = res.data.follow
				}
			})
		}
	}
}
